import React, { useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import { Menu, X, Newspaper, Moon, Sun, Languages } from 'lucide-react';
import { useTheme } from '../contexts/ThemeContext';
import { useLanguage } from '../contexts/LanguageContext';
import { useAuth } from '../contexts/AuthContext';
import { AuthButtons } from './auth/AuthButtons';
import { UserMenu } from './auth/UserMenu';

const Navbar: React.FC = () => {
  const [isOpen, setIsOpen] = useState(false);
  const location = useLocation();
  const { theme, toggleTheme } = useTheme();
  const { toggleLanguage, t } = useLanguage();
  const { user } = useAuth();

  const navItems = [
    { path: '/', label: t.home },
    { path: '/analyze', label: t.analyze },
    { path: '/trending', label: t.trending },
  ];

  return (
    <nav className="bg-gray-800/95 backdrop-blur-lg sticky top-0 z-50 shadow-lg">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex items-center justify-between h-16">
          {/* Logo */}
          <div className="flex-shrink-0">
            <Link to="/" className="flex items-center space-x-2">
              <Newspaper className="h-8 w-8 text-blue-500" />
              <span className="text-xl font-bold text-white hidden sm:block">
                {t.title}
              </span>
            </Link>
          </div>

          {/* Desktop Navigation */}
          <div className="hidden md:flex md:items-center md:space-x-4">
            {navItems.map(({ path, label }) => (
              <Link
                key={path}
                to={path}
                className={
                  location.pathname === path
                    ? 'bg-blue-600 text-white px-3 py-2 rounded-md text-sm font-medium transition-colors duration-200'
                    : 'text-gray-300 hover:bg-gray-700 hover:text-white px-3 py-2 rounded-md text-sm font-medium transition-colors duration-200'
                }
              >
                {label}
              </Link>
            ))}
          </div>

          {/* Desktop Actions */}
          <div className="hidden md:flex md:items-center md:space-x-4">
            <button
              onClick={toggleLanguage}
              className="p-2 rounded-md text-gray-300 hover:bg-gray-700 hover:text-white transition-colors duration-200"
            >
              <Languages className="h-5 w-5" />
            </button>
            <button
              onClick={toggleTheme}
              className="p-2 rounded-md text-gray-300 hover:bg-gray-700 hover:text-white transition-colors duration-200"
            >
              {theme === 'dark' ? (
                <Sun className="h-5 w-5" />
              ) : (
                <Moon className="h-5 w-5" />
              )}
            </button>
            <div className="ml-4">
              {user ? <UserMenu /> : <AuthButtons />}
            </div>
          </div>

          {/* Mobile menu button */}
          <div className="md:hidden">
            <button
              onClick={() => setIsOpen(!isOpen)}
              className="p-2 rounded-md text-gray-300 hover:bg-gray-700 hover:text-white transition-colors duration-200"
            >
              {isOpen ? (
                <X className="h-6 w-6" />
              ) : (
                <Menu className="h-6 w-6" />
              )}
            </button>
          </div>
        </div>
      </div>

      {/* Mobile menu */}
      <div className={isOpen ? 'md:hidden block' : 'md:hidden hidden'}>
        <div className="px-2 pt-2 pb-3 space-y-1 sm:px-3 bg-gray-800">
          {navItems.map(({ path, label }) => (
            <Link
              key={path}
              to={path}
              onClick={() => setIsOpen(false)}
              className={
                location.pathname === path
                  ? 'bg-blue-600 text-white block px-3 py-2 rounded-md text-base font-medium transition-colors duration-200'
                  : 'text-gray-300 hover:bg-gray-700 hover:text-white block px-3 py-2 rounded-md text-base font-medium transition-colors duration-200'
              }
            >
              {label}
            </Link>
          ))}
          <div className="flex items-center justify-between px-3 py-2">
            <button
              onClick={toggleLanguage}
              className="p-2 rounded-md text-gray-300 hover:bg-gray-700 hover:text-white transition-colors duration-200"
            >
              <Languages className="h-5 w-5" />
            </button>
            <button
              onClick={toggleTheme}
              className="p-2 rounded-md text-gray-300 hover:bg-gray-700 hover:text-white transition-colors duration-200"
            >
              {theme === 'dark' ? (
                <Sun className="h-5 w-5" />
              ) : (
                <Moon className="h-5 w-5" />
              )}
            </button>
            <div>
              {user ? <UserMenu /> : <AuthButtons />}
            </div>
          </div>
        </div>
      </div>
    </nav>
  );
};

export default Navbar; 