import React, { useState, useEffect } from 'react';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { Newspaper, Moon, Sun, Languages } from 'lucide-react';
import { useTheme } from '../contexts/ThemeContext';
import { useLanguage } from '../contexts/LanguageContext';
import { useAuth } from '../contexts/AuthContext';
import { translations } from '../locales/translations';
import { BackgroundAnimation } from './BackgroundAnimation';
import { AuthButtons } from './auth/AuthButtons';
import { UserMenu } from './auth/UserMenu';
import { useDrag } from '@use-gesture/react';
import Navbar from './Navbar';

interface Props {
  children: React.ReactNode;
}

export default function Layout({ children }: Props) {
  const { theme, toggleTheme } = useTheme();
  const { language, toggleLanguage } = useLanguage();
  const { user } = useAuth();
  const location = useLocation();
  const t = translations[language];
  const [isScrolled, setIsScrolled] = useState(false);
  const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 });
  const navigate = useNavigate();
  const [touchStart, setTouchStart] = useState<number | null>(null);
  const [touchEnd, setTouchEnd] = useState<number | null>(null);

  // Check if we're on the admin page
  const isAdminPage = location.pathname === '/admin';

  // Minimum swipe distance for navigation (in pixels)
  const minSwipeDistance = 50;

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 0);
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const handleMouseMove = (e: React.MouseEvent) => {
    const rect = e.currentTarget.getBoundingClientRect();
    const x = ((e.clientX - rect.left) / rect.width) * 20 - 10;
    const y = ((e.clientY - rect.top) / rect.height) * 20 - 10;
    setMousePosition({ x, y });
  };

  const handleMouseLeave = () => {
    setMousePosition({ x: 0, y: 0 });
  };

  const navItems = [
    { path: '/', label: t.home },
    { path: '/analyze', label: t.analyze },
    { path: '/trending', label: t.trending },
  ];

  const bind = useDrag(({ movement: [mx], direction: [dx], distance }) => {
    if (distance > minSwipeDistance) {
      if (dx > 0) {
        // Swipe right - go back
        navigate(-1);
      } else {
        // Swipe left - go forward
        navigate(1);
      }
    }
  });

  const onTouchStart = (e: React.TouchEvent) => {
    setTouchEnd(null);
    setTouchStart(e.targetTouches[0].clientX);
  };

  const onTouchMove = (e: React.TouchEvent) => {
    setTouchEnd(e.targetTouches[0].clientX);
  };

  const onTouchEnd = () => {
    if (!touchStart || !touchEnd) return;
    
    const distance = touchStart - touchEnd;
    const isLeftSwipe = distance > minSwipeDistance;
    const isRightSwipe = distance < -minSwipeDistance;

    if (isLeftSwipe) {
      navigate(1);
    }
    if (isRightSwipe) {
      navigate(-1);
    }
  };

  return (
    <div 
      className="min-h-screen flex flex-col bg-gradient-to-b from-gray-900 to-gray-800 text-white"
      {...bind()}
      onTouchStart={onTouchStart}
      onTouchMove={onTouchMove}
      onTouchEnd={onTouchEnd}
    >
      <Navbar />
      <main className="flex-1 container mx-auto px-4 py-6 overflow-x-hidden">
        <div className="max-w-7xl mx-auto">
          {children}
        </div>
      </main>
      <footer className="py-4 text-center text-gray-400 text-sm">
        <p> 2024 YEAH. All rights reserved.</p>
      </footer>
    </div>
  );
}